import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {QuestionDetailPage} from "../question-detail/question-detail";


// @IonicPage()
@Component({
  selector: 'page-ask-publish',
  templateUrl: 'ask-publish.html',
})
export class AskPublishPage {
  questionList = [
    {
      title: '老年抑郁症状是什么？',
      author: '苏明玉',
      date: '1559139754000',
      praise: 55,
      comment: 82
    },
    {
      title: '老年人要社交吗？',
      author: '蔡根花',
      date: '1559139754000',
      praise: 156,
      comment: 34
    },
    {
      title: '老年人会得抑郁症吗？',
      author: '苏大强',
      date: '1559139750004',
      praise: 11,
      comment: 23
    }
  ];
  sameList = [
    {
      title: '老年抑郁症状是什么？'
    },
    {
      title: '老年人要社交吗？'
    }
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.navCtrl.remove(1,3);
  }

  ionViewDidLoad() {
  }

  back() {
    this.navCtrl.pop();
  }

  goDetail(obj) {
    this.navCtrl.push(QuestionDetailPage, obj);
  }

}



