import {Component} from '@angular/core';
import {NavController, ToastController} from 'ionic-angular';
import {TabsPage} from "../tabs/tabs";
import {MeStudentPage} from "../me-student/me-student";
import {MeAccountPage} from "../me-account/me-account";
import {NotificationSendPage} from "../notification-send/notification-send";

@Component({
  selector: 'page-me',
  templateUrl: 'me.html'
})
export class MePage {

  categoryList = [{name: '我的学员', icon: 'assets/icon/icon-persons.png'},
    {name: '我的账户', icon: 'assets/icon/icon-account.png'},
    {name: '我的通知', icon: 'assets/icon/icon-horn-green.png'},
    {name: '设置', icon: 'assets/icon/icon-setting.png'}];
  studentList = [
    {
      id: 1,
      name: '徐涛',
      company: '护理-p',
      work: '护理员',
      lesson: '职业认知',
      progress: '70',
      start: '2019-04-23'
    },
    {
      id: 2,
      name: '赵江南',
      company: '护理-p',
      work: '护理员',
      lesson: '职业认知',
      progress: '28',
      start: '2019-04-23'
    }
  ];
  noticeList = [{
    id: 4,
    title: '关于开展护理知识的通知',
    hits: 101,
    reply: 24
  },
    {
      id: 5,
      title: '关于开展老年人饮食健康知识的通知',
      hits: 101,
      reply: 24
    }];
  userInfo = {
    name: '郭明浩',
    period: '一年',
    start: '2019-05-01',
    end: '2020-05-01'
  };
  count = 10;

  constructor(public navCtrl: NavController, public toastCtrl: ToastController) {

  }

  ionViewDidEnter(){
    TabsPage.changeTab(4);
  }

  addNotice = function () {
    this.noticeList.push({
      id: this.count++,
      title: '关于开展护理通知' + this.count,
      hits: 101,
      reply: 24
    });
    TabsPage.changeTab(5);
    this.navCtrl.push(NotificationSendPage);
  }

  deleteNotice = function (id) {
    console.log(id);
    var index = 0;
    for (var i = 0; i < this.noticeList.length; i++) {
      if (this.noticeList[i].id == id) {
        index = i
      }
    }
    this.noticeList.splice(index, 1);
    this.presentToast('成功删除 1 条通知');
  }

  goMyStudent = function () {
    TabsPage.changeTab(5);
    this.navCtrl.push(MeStudentPage);
  }

  goMeAccount = function () {
    TabsPage.changeTab(5);
    this.navCtrl.push(MeAccountPage);
  }

}



