import {Component, NgModule} from '@angular/core';
import {IonicModule, NavController} from 'ionic-angular';
import {HeaderPage} from "../common/header";
import {TabsPage} from "../tabs/tabs";
import {SearchPage} from "../search/search";
import {NotificationMePage} from "../notification-me/notification-me";

@Component({
  selector: 'page-notification',
  templateUrl: 'notification.html'
})
@NgModule({
  declarations: [],
  imports: [
    IonicModule,HeaderPage
  ],
  exports: [],
  providers: []
})
export class NotificationPage {
  categoryList = [{name: '我的通知', icon: 'assets/icon/icon-horn.png'},
    {name: '我的消息', icon: 'assets/icon/icon-bell.png'},
    {name: '我的私信', icon: 'assets/icon/icon-info.png'}];
  notificationList = [
    [{
      id: 1,
      title: '关于组织学习老年人护理的通知',
      content: '为提升我院护理人员知识水平，先请全体学员学习 “老年护具”使用课程。',
      time: '1559119750004',
      sender: '苏明哲',
      readed: true
    },{
      id: 2,
      title: '关于组织学习的饮食搭配通知',
      content: '为提升我院护理人员知识水平，先请全体学员学习 “老年护具”使用课程。',
      time: '1559129750004',
      sender: '杨晓琼',
      readed: false
    },{
      id: 3,
      title: '关于组织学习的保健按摩通知',
      content: '为提升我院护理人员知识水平，先请全体学员学习 “老年护具”使用课程。',
      time: '1559159750004',
      sender: '李洪明',
      readed: true
    }],
    [{
      id: 4,
      content: '您的话题“老年抑郁症状有哪些”已有唐山养老院、河北养老院等25人回答',
    },{
      id: 5,
      content: '您回复的“老年人饮食健康”已有30人点赞',
    }],
    [{
      id: 6,
      content: '唐山养老院发来一条私信',
    }],
  ];
  constructor(public navCtrl: NavController) {

  }

  ionViewDidEnter(){
    TabsPage.changeTab(3);
  }

  more = function () {
    alert('more');
  }

  goSearch = function() {
    this.navCtrl.push(SearchPage, {index: 3}, {animate:false});
  }

  goDetail(obj) {
    this.navCtrl.push(NotificationMePage, obj);
  }
}



