import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {BaseUI} from "../common/baseui";
import {LoadingController, ToastController} from "ionic-angular";

/**
 * Generated class for the NotificationSendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-notification-send',
  templateUrl: 'notification-send.html',
})
export class NotificationSendPage extends BaseUI{
  customPickerOptions: any;
  typeTxt:any;
  typeDataArr = [{id:1,type:'所有人'}, {id:2,type:'我的学生'}];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public toastCtrl: ToastController,
              public loadingCtrl: LoadingController) {
    super();
    this.customPickerOptions = {
      buttons: [{
        text: 'Save',
        handler: () => console.log('Clicked Save!')
      }, {
        text: 'Log',
        handler: () => {
          console.log('Clicked Log. Do not Dismiss.');
          return false;
        }
      }]
    }
  }

  ionViewDidLoad() {
  }

  back() {
    this.navCtrl.pop();
  }

  send() {
    this.showToast(this.toastCtrl, '发布成功', 'top');
  }

  /*选择select的value*/
  switchType() {
    console.log(this.typeTxt);
  }

}



