import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {AskPublishPage} from "../ask-publish/ask-publish";

/**
 * Generated class for the AskTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-ask-type',
  templateUrl: 'ask-type.html',
})
export class AskTypePage {
  searchValue;
  searchContent;
  typeList = ['养老理念', '护理技巧', '生活经验', '老年心理', '老年社交', '老年情感'];
  selectIndex = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.searchValue = navParams.get('words');
    this.searchContent = navParams.get('content');
  }

  ionViewDidLoad() {
  }

  submit() {
    this.navCtrl.push(AskPublishPage);
  }

  back() {
    this.navCtrl.pop();
  }

}



