import {NgModule, ErrorHandler} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {IonicApp, IonicModule, IonicErrorHandler} from 'ionic-angular';
import {MyApp} from './app.component';

import {HomePage} from '../pages/home/home';
import {LessonPage} from '../pages/lesson/lesson';
import {NotificationPage} from '../pages/notification/notification';
import {MePage} from '../pages/me/me';
import {HeaderPage} from '../pages/common/header';
import {TabsPage} from '../pages/tabs/tabs';


import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {SearchPage} from "../pages/search/search";
import {SearchResultPage} from "../pages/search-result/search-result";
import {AskPage} from "../pages/ask/ask";
import {AskTypePage} from "../pages/ask-type/ask-type";
import {AskPublishPage} from "../pages/ask-publish/ask-publish";
import {QuestionDetailPage} from "../pages/question-detail/question-detail";
import {LessonDetailPage} from "../pages/lesson-detail/lesson-detail";
import {NotificationMePage} from "../pages/notification-me/notification-me";
import {LessonMePage} from "../pages/lesson-me/lesson-me";
import {MeStudentPage} from "../pages/me-student/me-student";
import {PipeStudentPipe} from "../pipes/pipe-student/pipe-student";
import {MeAccountPage} from "../pages/me-account/me-account";
import {NotificationSendPage} from "../pages/notification-send/notification-send";

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LessonPage,
    NotificationPage,
    MePage,
    HeaderPage,
    TabsPage,
    SearchPage,
    SearchResultPage,
    AskPage,
    AskTypePage,
    AskPublishPage,
    QuestionDetailPage,
    LessonDetailPage,
    NotificationMePage,
    LessonMePage,
    MeStudentPage,
    PipeStudentPipe,
    MeAccountPage,
    NotificationSendPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {
      // backButtonText: '返回',
      tabsPlacement: 'bottom',
      tabsHideOnSubPages: 'true' // 隐藏全部子页面tabs
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LessonPage,
    NotificationPage,
    MePage,
    HomePage,
    HeaderPage,
    TabsPage,
    SearchPage,
    SearchResultPage,
    AskPage,
    AskTypePage,
    AskPublishPage,
    QuestionDetailPage,
    LessonDetailPage,
    NotificationMePage,
    LessonMePage,
    MeStudentPage,
    MeAccountPage,
    NotificationSendPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {
}



