import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {SearchResultPage} from "../search-result/search-result";

@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage {
  searchValue;
  searchHistory = ['护理人员', '老年人', '老年护理', '养老', '养老院', '人口老龄化'];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    var ionInput = document.getElementById('search-input');
    var input = ionInput.children[0] as HTMLElement;
    input.focus();

    setTimeout(function () {
      input.focus();
    }, 500);
  }

  ask = function () {
    if (this.searchValue) {
      // alert('提问' + this.searchValue);
      this.navCtrl.push(SearchResultPage, {words: this.searchValue});
    } else {
      this.navCtrl.pop({animate: false});
    }
  }

  wrong = function () {
    this.searchValue = '';
  }

  trash = function () {
    this.searchHistory = [];
  }

  ionViewDidEnter() {
  }

}



