import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';


// @IonicPage()
@Component({
  selector: 'page-me-student',
  templateUrl: 'me-student.html',
})
export class MeStudentPage {
  searchValue = '';
  studentList = [
    {
      id: 1,
      name: '徐涛',
      company: '护理-p',
      work: '护理员',
      lesson: '职业认知',
      progress: '70',
      start: '2019-04-23'
    },
    {
      id: 2,
      name: '赵江南',
      company: '护理-p',
      work: '护理员',
      lesson: '职业认知',
      progress: '28',
      start: '2019-04-23'
    }
  ];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
  }

  back() {
    this.navCtrl.pop();
  }

  addStudent = function () {
    this.studentList.push(
      {
        id: this.count++,
        name: '学员' + this.count,
        company: '护理-p' + this.count,
        work: '护理员' + this.count,
        lesson: '职业认知' + this.count,
        progress: 28 + this.count,
        start: '2019-04-23'
      });
    this.presentToast('成功添加 1 位学员');
  }

}



